import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Patient } from '../models/patient';
import { Convert, MedicationRequest } from '../models/medication-result';
import { MedicationService } from '../services/medication.service';
import { MedicationOrderRequest } from '../models/medication-order-request';
import { PatientConvert, PumpMapDetails } from '../models/PumpMapDetails';
import { LoginService } from '../services/login/login.service';

export interface MedicationRequestModel {
  PatientID: string;
  PatientName: string;
  MedicationReqID: string;
  MedicationItem: string;
  DosageInstruction: string;
  Category: string;
  AuthorisedDate: string;
  Status: string;
}
@Component({
  selector: 'app-patient-map',
  templateUrl: './patient-map.component.html',
  styleUrls: ['./patient-map.component.css']
})

export class PatientMapComponent implements OnInit {

  dataSource: MedicationRequestModel[] = [];
  private patients: Patient;
  private selectedPatient = [];
  private selectedPatientId: string = '';
  private startDateTime: string = '';
  private encounterList = [];
  private ivSites: string[] = [];
  private bedNames: string[] = [];
  private displayDialog: boolean = false;
  private displayMedicationOrderDialog: boolean = false;
  private interval;
  private itemLoaded: boolean = true;
  private pumpStartTypeButtonToggle: string = 'manual';
  private patientName: string = '';
  private bedName: string = '';
  private ivSite: string = '';
  private isSubmitButtonDisabled: boolean = true;
  private pumpMapDetails: PumpMapDetails = new PumpMapDetails();
  isSaved = false;
  private selectedPatientID: string = '';
  patientId = '';
  private allPumpStatus: MedicationOrderRequest[] = [];
  private pumpList = [
    {
      'patientName': '',
      'pumpStartType': '',
      'patientId': '',
      'pumpName': 'Pump-A',
      'bednumber': '',
      'ivSite': '',
      'pumpStatus': 'A',
    },
    {
      'patientName': '',
      'pumpStartType': '',
      'patientId': '',
      'pumpName': 'Pump-B',
      'bednumber': '',
      'ivSite': '',
      'pumpStatus': 'A',
    },
    {
      'patientName': '',
      'pumpStartType': '',
      'patientId': '',
      'pumpName': 'Pump-C',
      'bednumber': '',
      'ivSite': '',
      'pumpStatus': 'A',
    },
    {
      'patientName': '',
      'pumpStartType': '',
      'patientId': '',
      'pumpName': 'Pump-D',
      'bednumber': '',
      'ivSite': '',
      'pumpStatus': 'A',
    },
    {
      'patientName': '',
      'pumpStartType': '',
      'patientId': '',
      'pumpName': 'Pump-E',
      'bednumber': '',
      'ivSite': '',
      'pumpStatus': 'A',
    },
    {
      'patientName': '',
      'pumpStartType': '',
      'patientId': '',
      'pumpName': 'Pump-F',
      'bednumber': '',
      'ivSite': '',
      'pumpStatus': 'A',
    },
  ];

  constructor(private medicationService: MedicationService,  private router: Router, private login: LoginService) { }
  ngOnInit() {
    this.medicationService.getPumpDetails().subscribe(response => {
      let resultData: PumpMapDetails[] = PatientConvert.toPatientDetails(JSON.stringify(response));
      console.log(resultData);
      if (resultData) {
        this.updatePumpMapDetails(resultData);
      }
    });

    this.interval = setInterval(() => {
      this.medicationService.getAllPumpStatus().subscribe(response => {
        this.allPumpStatus = response;
        if(this.allPumpStatus.length > 0){
          this.itemLoaded = false;
        }  
      });
    }, 5000);
  }
  valueChanged(value: string) {
    this.patientId = value;
  }
  updatePumpMapDetails(resultData: any) {
    for (let patient of resultData) {
      for (let pumpItem in this.pumpList) {
         if (patient.pumpname == this.pumpList[pumpItem].pumpName) {
          this.pumpList[pumpItem].pumpStatus = patient.pumpstatus;
          this.pumpList[pumpItem].patientName = patient.patientname;
          this.pumpList[pumpItem].patientId = patient.patientid;
          this.pumpList[pumpItem].bednumber = patient.bednumber;
          this.pumpList[pumpItem].ivSite = patient.ivsite;
         }
      }
    }
  }

  getPatientInfo(patientDetails: any) {
    for (let patient of patientDetails) {
      let name = patient.resource.name.map(a => a.text);
      this.selectedPatient.push(name);
    }
  }

  searchItem(event) {    
  this.isSaved = false;
    this.medicationService.getPatientInfo(event.query).subscribe(response => {
      this.patients = JSON.parse(JSON.stringify(response));
      if (this.patients != null && this.patients.entry != null && this.patients.entry.length > 0) {
        this.getPatientInfo(this.patients.entry);
        this.selectedPatient = this.selectedPatient.filter(a => a.filter(b => b.toLowerCase().includes(event.query.toLowerCase())));
      }
    });
  }
  
  filterBedNames() {
    this.bedNames = ['Bed 1', 'Bed 2', 'Bed 3', 'Bed 4', 'Bed 5', 'Bed 6'];
  }

  filterIvSites() {
    this.ivSites = ['Hand', 'Wrist', 'Foot', 'Leg', 'Scalp'];
  }

  openMapPatientDialog(pumpData: any) {
    if (pumpData.pumpStatus &&  pumpData.pumpStatus == 'L' || pumpData.pumpStatus == 'R' || pumpData.pumpStatus == 'S' || pumpData.pumpStatus == 'C') {
      const navigationDetails: string[] = ['/medication-orders'];
      this.router.navigate(navigationDetails, { queryParams:  pumpData});
    } else {
      this.displayDialog = true;
      this.patientName = '';
      this.bedName = '';
      this.ivSite = '';
      this.isSubmitButtonDisabled = true;
      this.pumpMapDetails = new PumpMapDetails();
      this.pumpMapDetails.pumpStartType = this.pumpStartTypeButtonToggle;
      this.pumpMapDetails.pumpName = pumpData.pumpName;
    }
  }

  closeMapPatientDialog() {
    this.displayDialog = false;
    this.patients = null;
  }

  submitPatientData() {
    if (this.patientName && this.ivSite && this.bedName){
      this.pumpMapDetails.patientId = this.selectedPatientId;
      this.pumpMapDetails.patientName = this.patientName;
      this.pumpMapDetails.pumpStartType = this.pumpStartTypeButtonToggle;
      this.pumpMapDetails.bednumber = this.bedName;
      this.pumpMapDetails.ivSite = this.ivSite;
      this.pumpMapDetails.pumpStatus = 'L';
      for (let pumpItem of this.pumpList) {
        if (pumpItem.pumpName == this.pumpMapDetails.pumpName) {
          pumpItem.pumpStatus = 'L';
          pumpItem.patientId = this.selectedPatientId;
          pumpItem.patientName = this.patientName;
          pumpItem.pumpStartType = this.pumpStartTypeButtonToggle;
          pumpItem.bednumber = this.bedName;
          pumpItem.ivSite = this.ivSite;
        }
      }
      this.medicationService.updatePatientDetails(this.pumpMapDetails).subscribe(response => {
       });
       this.closeMapPatientDialog();
    }
  }

  onSelectDropdown() {
    if (this.patients != null && this.patients.entry != null) {
      let currentPatient = this.patients.entry.filter(a => a.resource.name.find(b => b.text.includes(this.patientName)));
      if (currentPatient[0]) {
        this.selectedPatientId  = currentPatient[0].resource.id;
        this.updateSubmitButtonDisabledProperty();
      }
    }
  }
  onSelectPatientName(event: any) {
    this.pumpMapDetails.patientId = this.patients.id;
    this.pumpMapDetails.patientName = event[0];
    this.patientName = event[0];
    this.updateSubmitButtonDisabledProperty();
  }

  onSelectBedName(event: any) {
    this.bedName = event;
    this.pumpMapDetails.bednumber = event;
    this.updateSubmitButtonDisabledProperty();
  }

  onSelectIvSite(event: any) {
    this.ivSite = event;
    this.pumpMapDetails.ivSite = event;
    this.updateSubmitButtonDisabledProperty();
  }

  public onValChange(val: string) {
    this.pumpStartTypeButtonToggle = val;
  }

  updateSubmitButtonDisabledProperty() {
    if (!(this.patients.id && this.patientName && this.pumpStartTypeButtonToggle && this.bedName && this.ivSite && this.pumpMapDetails.pumpName)) {
      this.isSubmitButtonDisabled = false;
    }
  }

  showMappedPatientDetails(){
    const navigationDetails: string[] = ['/medication-orders'];
    this.router.navigate(navigationDetails);
  }
  cancelMedicationOrder(event: any) {    
    this.isSaved = true;
    this.displayMedicationOrderDialog = false;
    this.getAssociatedMedicalRequests(this.patients.entry, this.patientName);    
  }
  getAssociatedMedicalRequests(patientDetails: any, selectedName: string) {
    for (let patient of patientDetails) {
      let id = patient.resource.id;
      var name = patient.resource.name[0].text;
      if (selectedName == name) {
        this.selectedPatientID = id;
        this.medicationService.getMedicationResult(id).subscribe(response => {
          let resultData: MedicationRequest = Convert.toMedicationRequest(JSON.stringify(response));
          if (resultData != null && resultData.entry != null && resultData.entry.length > 0) {
            this.bindMedicationValue(resultData.entry);
          }
        });
      }
      this.getEncounterByPatientId(id);
    }
  }
  bindMedicationValue(sources: any) {
    for (let data of sources) {
      let medicationData: MedicationRequestModel = { PatientID: '', PatientName: '', MedicationReqID: '', MedicationItem: '', DosageInstruction: '', Category: '', AuthorisedDate: '', Status: '' };
      medicationData.PatientName = data.resource.subject.display;
      medicationData.PatientID = data.resource.subject.reference.split('/')[1];
      medicationData.MedicationReqID = data.resource.id;
      medicationData.MedicationItem = data.resource.medicationCodeableConcept.text
      medicationData.DosageInstruction = data.resource.dosageInstruction[0].text;
      medicationData.Category = data.resource.category[0].coding[0].display;
      medicationData.AuthorisedDate = data.resource.authoredOn;
      medicationData.Status = data.resource.status;
      this.dataSource.push(medicationData);
    }
  }
  getMedicationResult(event: any) {
    this.dataSource = [];
    this.getAssociatedMedicalRequests(this.patients.entry, event[0])
  }
  getEncounterByPatientId(patientId:any)
  {
    this.encounterList = [];
    this.medicationService.getEncountersByPatientId(patientId).subscribe(response => {
      let resultData: MedicationRequest = Convert.toMedicationRequest(JSON.stringify(response));
      if (resultData != null && resultData.entry != null && resultData.entry.length > 0) {
        
        for(let encounter of resultData.entry)
        {
          this.encounterList.push({id:encounter.resource.id});
        }
      }
    });
  }  
}
export { PumpMapDetails };

