import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { PatientMapComponent } from './patient-map/patient-map.component';
import { PumpdetailsComponent } from './pumpdetails/pumpdetails.component';

const routes: Routes = [
  { path: '', component: PatientMapComponent },
  { path: 'pump-listing', component: PatientMapComponent },
  { path: 'medication-orders', component: PumpdetailsComponent },
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { 
  
}
