export class PumpMapDetails {
    patientName: string;
    pumpStartType: string;
    patientId: string;
    pumpName: string;
    bednumber: string;
    ivSite: string;
    pumpStatus: string;
}

  export class PatientConvert {
    public static toPatientDetails(json: string): PumpMapDetails[] {
      return JSON.parse(json);
    }
  }