import { Component, OnInit } from '@angular/core';
import { Convert, MedicationRequest } from '../models/medication-result';
import { MedicationService } from '../services/medication.service';

export interface MedicationModel {
  patientName: string;
  patientId: string;
  doseType: string;
  drug: string;
  doseQuantity: string;
  time: string;
  status: string;
}

@Component({
  selector: 'app-homepage',
  templateUrl: './homepage.component.html',
  styleUrls: ['./homepage.component.css']
})

export class HomepageComponent implements OnInit {
  patientId = '';
  displayedColumns: string[] = ['patientId', 'patientName', 'doseType', 'drug', 'doseQuantity',  'time', 'status'];
  dataSource: MedicationModel[] = [];
  isDataLoaded: boolean = false;

  constructor(private medicationService: MedicationService) { }

  ngOnInit() {
  }

  valueChanged(value: string) {
    this.patientId = value;
  }

  getMedicationResult() {
    this.dataSource = [];
    this.isDataLoaded = false;
    this.medicationService.getMedicationResult(this.patientId).subscribe(response => {
      let resultData: MedicationRequest = Convert.toMedicationRequest(JSON.stringify(response));
      this.isDataLoaded = true;
      if (resultData != null && resultData.entry != null && resultData.entry.length > 0) {
         this.bindMedicationValue(resultData.entry);
      }
  });
  }

  bindMedicationValue(sources: any) {
    for (let data of sources) {
      let medicationData: MedicationModel = { patientId: '', patientName: '', drug: '', time: '' , status: '', doseType: '', doseQuantity: '' };
      medicationData.patientName = data.resource.subject.display;
      medicationData.patientId = data.resource.subject.reference.split('/')[1];
      medicationData.status = data.resource.status;
      medicationData.doseType = data.resource.dosageInstruction[0].route.text;
      medicationData.time = data.resource.dosageInstruction[0].timing.repeat.boundsPeriod.start;
      medicationData.doseQuantity = data.resource.dosageInstruction[0].doseAndRate[0]!.rateQuantity!.value + data.resource.dosageInstruction[0].doseAndRate[0]!.rateQuantity!.unit;
      if (medicationData.status == 'active' && medicationData.doseType.includes('IV')) {
        this.dataSource.push(medicationData);
      }
    }
  }
}
