export class MedicationRequest {
    resourceType: string;
    id: string;
    type: string;
    link: Link[];
    entry: Entry[];
}

export interface Entry {
    fullUrl: string;
    resource: Resource;
    search: Search;
}

export interface Resource {
    resourceType: ResourceType;
    id: string;
    meta: Meta;
    text: Text;
    extension: ResourceExtension[];
    status: ResourceStatus;
    intent: Intent;
    category: Category[];
    reportedBoolean: boolean;
    medicationCodeableConcept: CourseOfTherapyType;
    subject: Subject;
    encounter: Encounter;
    authoredOn: Date;
    requester: Requester;
    dosageInstruction: DosageInstruction[];
    dispenseRequest: DispenseRequest;
    courseOfTherapyType?: CourseOfTherapyType;
    note?: Note[];
    substitution?: Substitution;
}

export interface Category {
    coding: Coding[];
}

export interface Coding {
    system: string;
    code: string;
    display: string;
    userSelected?: boolean;
}

export interface CourseOfTherapyType {
    coding: Coding[];
    text: string;
}

export interface DispenseRequest {
    extension: DispenseRequestExtension[];
    validityPeriod: Period;
    numberOfRepeatsAllowed?: number;
}

export interface DispenseRequestExtension {
    valueBoolean: boolean;
    url: string;
}

export interface Period {
    start: Date;
}

export interface DosageInstruction {
    extension: DosageInstructionExtension[];
    text: string;
    patientInstruction: string;
    timing: Timing;
    route?: CourseOfTherapyType;
    doseAndRate?: DoseAndRate[];
    asNeededCodeableConcept?: CourseOfTherapyType;
    additionalInstruction?: Note[];
}

export interface Note {
    text: string;
}

export interface DoseAndRate {
    doseQuantity: DoseQuantity;
}

export interface DoseQuantity {
    value: number;
    unit: string;
    system: string;
    code: string;
}

export interface DosageInstructionExtension {
    valueString: string;
    url: string;
}

export interface Timing {
    repeat: Repeat;
    code?: CourseOfTherapyType;
}

export interface Repeat {
    boundsPeriod: Period;
}

export interface Encounter {
    reference: EncounterReference;
}

export enum EncounterReference {
    Encounter97953530 = "Encounter/97953530",
}

export interface ResourceExtension {
    valueCoding?: Coding;
    url: string;
    valueCodeableConcept?: Note;
}

export enum Intent {
    Order = "order",
    Plan = "plan",
}

export interface Meta {
    versionId: string;
    lastUpdated: Date;
}

export interface Requester {
    extension?: RequesterExtension[];
    reference?: string;
    display?: string;
}

export interface RequesterExtension {
    valueCode: ValueCode;
    url: string;
}

export enum ValueCode {
    Unknown = "unknown",
}

export enum ResourceType {
    MedicationRequest = "MedicationRequest",
}

export enum ResourceStatus {
    Active = "active",
}

export interface Subject {
    reference: SubjectReference;
    display: Display;
}

export enum Display {
    PetersTimA = "PETERS, TIM A",
}

export enum SubjectReference {
    Patient12742400 = "Patient/12742400",
}

export interface Substitution {
    allowedBoolean: boolean;
}

export interface Text {
    status: TextStatus;
    div: string;
}

export enum TextStatus {
    Generated = "generated",
}

export interface Search {
    mode: Mode;
}

export enum Mode {
    Match = "match",
}

export interface Link {
    relation: string;
    url: string;
}

// Converts JSON strings to/from your types
export class Convert {
    public static toMedicationRequest(json: string): MedicationRequest {
        return JSON.parse(json);
    }

    public static medicationRequestToJson(value: MedicationRequest): string {
        return JSON.stringify(value);
    }
}
