import { HttpClient, HttpHeaders } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { Observable } from "rxjs";
import { AccessToken } from "src/app/models/accesstoken";
import { ApiEndPoint } from "src/config/endpoint";

@Injectable({
    providedIn: 'root'
})
  
export class LoginService {
    constructor(private httpClient: HttpClient, private endpoints: ApiEndPoint){
        
    }
    private tokenDetails: AccessToken = new AccessToken();
    setToken(){
        this.getToken(this.tokenDetails).subscribe(res => {
            localStorage.setItem('tokenResponse', JSON.stringify(res));
        });
    }
    
    getToken(tokenDetails: AccessToken): Observable<any>  {
        let header = new HttpHeaders({'Content-Type': 'application/x-www-form-urlencoded'});
        header.set('Access-Control-Allow-Origin', '*');
        let bodyString =  'grant_type=' + tokenDetails.grant_type + '&refresh_token=' + tokenDetails.refresh_token;
        let options = { headers: header };
        return this.httpClient.post(this.endpoints.authorization, bodyString, options );
    }
}