import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { ApiEndPoint } from 'src/config/endpoint';
import { Observable } from 'rxjs';
import { PumpMapDetails } from '../patient-map/patient-map.component';
import { MedicationOrderRequest } from '../models/medication-order-request';
import { MedicationRequest, Resource } from '../models/medication-result';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root'
})

export class MedicationService {

  apiUrl = '';
  pumpUrl = 'http://52.3.72.133:5000/';
  constructor(private httpClient: HttpClient, public endpointService: ApiEndPoint) { }

  getMedicationResult(patientId: string): Observable<any>  {
    this.apiUrl = this.endpointService.getMedication;
    return this.httpClient.get(this.apiUrl + patientId);
  }

  getPatientInfo(patientName: string): Observable<any>  {
    this.apiUrl = this.endpointService.getPatientList;
    return this.httpClient.get(this.apiUrl + patientName);
  }

  updatePatientDetails(patientDetails: PumpMapDetails): Observable<any>  {
    return this.httpClient.post(this.pumpUrl + "pump/create", patientDetails);
  }

  getPumpDetails(): Observable<any>  {
    return this.httpClient.get(this.pumpUrl + "pump/get");
  }

  startPump(pumpData: any): Observable<any> {
    return this.httpClient.post(this.pumpUrl + "pump/start", pumpData);
  }

  stopPump(pumpName: string): Observable<any> {
    return this.httpClient.post(this.pumpUrl + "pump/stop", pumpName);
  }
  
  getPumpStatus(pumpName: string): Observable<any> {
    return this.httpClient.get(this.pumpUrl + "tube/get?pumpname=" + pumpName);
  }
  getEncountersByPatientId(patientId: string): Observable<any>  {
    this.apiUrl = this.endpointService.encounter;
    return this.httpClient.get(this.apiUrl + patientId);
  }
  createMedicationOrder(medicationRequestDetail: Resource): Observable<any> {
    console.log(medicationRequestDetail);
    this.apiUrl = this.endpointService.medicationRequest;    
    return this.httpClient.post(this.apiUrl, medicationRequestDetail, {
      headers: new HttpHeaders()
      .set('Authorization', 'Bearer '+environment.bearerToken)
      .set('Access-Control-Allow-Origin', '*')
    });
  }
  getAllPumpStatus(): Observable<any> {
    return this.httpClient.get(this.pumpUrl + "tube/get/all");
  }
}
