import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Convert, MedicationRequest } from '../models/medication-result';
import { PumpMapDetails } from '../patient-map/patient-map.component';
import { MedicationService } from '../services/medication.service';
import { MedicationOrderRequest } from '../models/medication-order-request';

export class MedicationModel {
  id: string;
  drug: string;
  doseQuantity: string;
  rateQuantity: string;
  startTime: string;
  endTime: string;
  status: string;
  start: string;
  stop: string;
  frequency: string;
}

@Component({
  selector: 'app-pumpdetails',
  templateUrl: './pumpdetails.component.html',
  styleUrls: ['./pumpdetails.component.css']
})
export class PumpdetailsComponent implements OnInit {

  private screenHeight;
  private isMedicationStarted: boolean = false;
  private rowId: string;
  private interval;
  private pumpMapDetails: PumpMapDetails = new PumpMapDetails();
  displayedColumns: string[] = ['drug', 'doseQuantity', 'rateQuantity', 'startTime', 'frequency','status', 'start', 'stop'];
  dataSource: MedicationModel[] = [];
  isDataLoaded: boolean = false;
  private pumpStatus: MedicationOrderRequest = new MedicationOrderRequest();
  constructor(private route: ActivatedRoute, private medicationService: MedicationService ) { }

  ngOnInit() {
    this.screenHeight = window.innerHeight;
    this.route.queryParams.subscribe(params => {
      this.pumpMapDetails = JSON.parse(JSON.stringify(params));
    });

    this.medicationService.getPumpStatus(this.pumpMapDetails.pumpName).subscribe(response => {
      this.pumpStatus = response;  
    });

    this.interval = setInterval(() => {
      this.medicationService.getPumpStatus(this.pumpMapDetails.pumpName).subscribe(response => {
        this.pumpStatus = response;  
      });
    }, 5000);

    this.medicationService.getMedicationResult(this.pumpMapDetails.patientId).subscribe(response => {
      let resultData: MedicationRequest = Convert.toMedicationRequest(JSON.stringify(response));
      if (typeof(resultData.entry) !== "undefined"){
        this.prepareDataSource(resultData.entry);
      }
  });
  }

  prepareDataSource(dataList) {
    let count = 0;
    var test = [];
    dataList = dataList.filter(x => x.resource.dosageInstruction[0].route != null && x.resource.dosageInstruction[0].route.text.includes("IV"));
    // console.log(dataList)
    for (let data of dataList) {
      var medicationData = new MedicationModel();
      medicationData.id = (count++).toString();
      medicationData.drug = data.resource.medicationCodeableConcept.text;
      medicationData.doseQuantity = data.resource.dosageInstruction[0].doseAndRate && data.resource.dosageInstruction[0].doseAndRate[0]!.doseQuantity!.value + ' ' + data.resource.dosageInstruction[0].doseAndRate[0]!.doseQuantity.unit;
      medicationData.rateQuantity = data.resource.dosageInstruction[0].doseAndRate && data.resource.dosageInstruction[0].doseAndRate[0].rateQuantity && data.resource.dosageInstruction[0].doseAndRate[0]!.rateQuantity!.value + ' ' + data.resource.dosageInstruction[0].doseAndRate[0]!.rateQuantity!.unit;
      medicationData.startTime = data.resource.dosageInstruction[0].timing.repeat.boundsPeriod.start;
      medicationData.status = data.resource.status;
      medicationData.start = '';
      medicationData.stop = '';
      medicationData.frequency = data.resource.dosageInstruction[0].timing.code != null ? data.resource.dosageInstruction[0].timing.code.text : '-';
      test.push(medicationData);
    }
    this.dataSource = test;
  }

  startMedication(data) {
      this.isMedicationStarted = true;
      this.rowId = data.id;
      let pumpStartData: any = {
          "pumpdetailid": this.pumpMapDetails.pumpName,
          "patientname": this.pumpMapDetails.patientName,
          "medication": data.drug,
          "startdatetime": data.startTime,
          "dosage":data.rateQuantity.split(' ')[1],
          "totalVolume": data.doseQuantity.split(' ')[0],
          "infusedvolume": 0.0,
          "ratevalue": data.rateQuantity.split(' ')[0],
          "ratedosage":data.doseQuantity.split(' ')[1],
          "ratehour":1.0,
          "ratecalculatevalue":0,
          "bedNo": this.pumpMapDetails.bednumber,
          "status": "L"
      };

      this.medicationService.startPump(pumpStartData).subscribe(response => {});
  }

  stopMedication(id) {
    this.isMedicationStarted = false;
    this.rowId = id;
    clearInterval(this.interval);
    this.medicationService.stopPump(this.pumpMapDetails.pumpName).subscribe(response => {});
  }
}