import { Injectable } from '@angular/core';
import { HttpInterceptor, HttpHandler, HttpRequest, HttpEvent, HttpErrorResponse } from '@angular/common/http';
import { Observable ,  throwError as throwError } from 'rxjs';
import { Router } from '@angular/router';
import { catchError } from 'rxjs/operators';
import { LoginService } from '../login/login.service';


@Injectable()
export class AuthInterceptor implements HttpInterceptor {

  constructor(private router: Router, private login: LoginService) { }

  intercept(req: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {
    // Get the auth token from the service.
    const authToken = JSON.parse(localStorage.getItem('tokenResponse'));
    if (authToken != null && authToken['access_token'] && req.url.includes('https://fhir-ehr.cerner.com/')) {
      req = req.clone({ setHeaders: { Authorization: 'Bearer ' + authToken['access_token'] } });
    }
    else{
      if (req.url.includes('/pump/get')) {
        if(authToken == null || authToken == undefined){
          this.login.setToken();
        }
      }
    }
    
    return next.handle(req).pipe(
      catchError((error: HttpErrorResponse) => {
        if (error.status === 401) {
          //this.login.setToken();
        }
        return throwError(error);
      })
    );
  }
}
